.slider {
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;

    &__arrow {
        padding: 3em;
        width: 50px;
        height: 40px;
    }

    &__arrow:hover {
        cursor: pointer;
    }
}

.arrow {
    width: 100%;
    height: 100%;
}